:root {
  font-family: $font-family-Open-Sans;
  font-size: $font-size-middle;
}

button {
  cursor: pointer;
  background-color: $color-opacity;
  border: 1px solid $color-grey-normal;
}

.btnLarge{
  font-weight: $light;
  text-transform: uppercase;
  padding: $space-small 0;
  font-size: $font-size-small;
}

input, textarea{
  cursor: pointer;
}